<template>
  <article class="game__charface">

    <div
      class="game__char game__char--flip"
      :style='{backgroundImage: `url(/img/game/${facepic}.jpg)`}'
      v-if="!targetStatus"
    >
      <img v-show="pop" class="game__char__icon2 game__char__icon2--enemy" :src="`/img/game/${facepic}.jpg`">
    </div>

    <div
      v-else
      @click="addToSkillQueue()"
      v-on-click-outside='cancel'
      class="game__char game__char--flip game__char--target"
    >
      <img :src="`/img/game/${facepic}.jpg`">
    </div>

    <Bars
      :health='health'
      :energy='energy'
    />

  </article>
</template>


<script>
import Bars from "@/components/_GaugeBars.vue";
import targetHandle from "@/helper/targetHandle";

export default {
  components: {
    Bars
  },
  mixins:[targetHandle],
  props: ["facepic"],
  computed: {
    targetStatus: function() {
      return this.$store.getters["targetMode/getTargetEnemyStatus"];
    }
  },
  methods: {
    cancel: function() {
      this.$store.commit("targetMode/RESET_STATE", {isAlly:false})
    },
  }
};
</script>