<template>
  <div
    class="game__skillset__pic"
    @click.self="selectSkill()"
    :style='{backgroundImage: `url(/img/game/${skill.skillpic}.jpg)`}'
  >
    <img
      v-if="selectedSkill.id === skill.id"
      class="game__skillset__pic--pop"
      :src="`/img/game/${skill.skillpic}.jpg`"
    >
    <span
      v-else-if="((selectedSkill.id!== undefined && selectedSkill.id !== skill.id) 
                || (skill.cooldown > 0) || !myturn || (character(caster.position).energy < skill.cost))
                || checkReqs"
      class="game__skillset--disabled"
      @click="$store.commit('targetMode/SET_SKILL_PREVIEW', skill)"
    >
      <p v-if="skill.cooldown > 0 && myturn">{{skill.cooldown}}</p>
    </span>
  </div>
</template>

<script>
export default {
  props: ["skill", "index", "caster"],
  data() {
    return {
      offensiveTarget: ["oneEnemy", "allEnemies"],
      helpfulTarget: ["self", "allAllies", "oneAlly", "otherAllies"]
    };
  },
  computed: {
    checkReqs: function() {
      if (this.skill.target.reqs !== undefined) {
        for (const i in this.skill.target.reqs) {
          switch (this.skill.target.reqs[i].param) {
            case "skill": {
              switch (this.skill.target.reqs[i].threshold) {
                case "self": {
                  if (
                    "Susanoo" in
                    this.$store.getters["ingame/getActiveEffects"](
                      this.caster.position,
                      "friends"
                    )
                  )
                    continue;
                  else return true;
                }
              }
            }
          }
        }
      }
      return false;
    },
    selectedSkill: function() {
      return this.$store.getters["targetMode/getSelectedSkillInfo"].skill;
    },
    myturn() {
      return this.$store.getters["ingame/getMyTurn"];
    },
    character() {
      return this.$store.getters["ingame/getOneFriendByIndex"];
    }
  },
  methods: {
    selectSkill() {
      let s = {
        caster: this.caster.id,
        position: this.caster.position,
        skill: this.skill,
        skillIndex: this.index
      };

      if (this.myturn && this.offensiveTarget.includes(this.skill.target.type))
        s.isAlly = false;
      else if (
        this.myturn &&
        this.helpfulTarget.includes(this.skill.target.type)
      )
        s.isAlly = true;
      this.$store.commit("targetMode/SET_SELECTION", s);
    }
  }
};
</script>