export default {
    props: ["index", "id", "health", "energy"],
    data() {
        return {
            pop: false
        }
    },
    methods: {
        oneEnemy: function () {
            return [
                {
                    target: this.id,
                    position: this.index
                }
            ]
        },

        otherAllies(self){
            const targets = []
            const friends = this.$store.getters["ingame/getMyFriends"];
            for (const i in friends) {
                if (friends[i].id !== self)
                    targets.push({
                        target: friends[i].id,
                        position: i
                    });
            }
            return targets
        },

        allAllies: function () {
            let targets = [
                {
                    target: this.id,
                    position: this.index
                }
            ];
            const friends = this.$store.getters["ingame/getMyFriends"];
            for (const i in friends) {
                if (friends[i].id !== this.id)
                    targets.push({
                        target: friends[i].id,
                        position: i
                    });
            }
            return targets
        },

        allEnemies: function () {
            let targets = [
                {
                    target: this.id,
                    position: this.index
                }
            ];
            const foes = this.$store.getters["ingame/getMyFoes"];
            for (const i in foes) {
                if (foes[i].id !== this.id)
                    targets.push({
                        target: foes[i].id,
                        position: i
                    });
            }
            return targets
        },

        self(){
            return this.oneEnemy()
        },
         
        oneAlly(){
            return this.oneEnemy()
        },

        addToSkillQueue() {
            const info = this.$store.getters["targetMode/getSelectedSkillInfo"]
            const selectFunction = info.skill.target.type
            const targets = this[selectFunction](info.caster)

            const payload = {
                skill: info.skill,
                skillIndex: info.skillIndex,
                targets: targets,
                caster: info.caster,
                position: info.position
            };
            this.$store.commit("ingame/ADD_TO_SKILL_QUEUE", payload);
            this.$store.commit("targetMode/RESET_STATE", true, true)
        }

    },
    watch: {
        health: function () {
            this.pop = true
            const self = this
            setTimeout(function () {
                self.pop = false
            }, 1000)
        }
    }
}