<template>
  <div class="game">

    <div class="game__heading">
      <h1>{{timer}}</h1>
      <button
        v-if="myturn"
        @click="endTurn"
      >End turn</button>
    </div>
    <section class="game__battle">
      <Allies></Allies>
      <Enemies></Enemies>
    </section>
    <div class="infobox">
      <p>
        {{selectedSkill.name}}
      </p>
      <p>
        {{selectedSkill.description}}
      </p>
    </div>
  </div>
</template>

<script>
import Enemies from "@/components/enemies";
import Allies from "@/components/allies";
export default {
  components: {
    Enemies,
    Allies
  },
  data() {
    return {
      timer: 60,
      interval: false
    };
  },
  sockets: {
    startTurn: function(payload) {
      this.endTimer();
      this.$store.commit("ingame/SET_GAME", payload);
      this.timer = 60;
      this.startTimer();
    },
    endTurn: function(payload) {
      this.endTimer();
      this.$store.commit("ingame/RESET_SKILL_QUEUE");
      this.$store.commit("ingame/SET_GAME", payload);
      this.$store.commit("targetMode/RESET_STATE", { all: true });
      this.timer = 60;
      this.startTimer();
    },
    forcedEndTurn: function() {
      this.$socket.client.emit("endTurn", []);
    }
  },
  computed: {
    myturn() {
      return this.$store.getters["ingame/getMyTurn"];
    },
    selectedSkill(){
      return this.$store.getters['targetMode/getSkillPreview']
    }
  },
  methods: {
    endTurn() {
      this.$socket.client.emit(
        "endTurn",
        this.$store.getters["ingame/getSkillQueue"]
      );
    },
    startTimer() {
      this.interval = setInterval(
        function(that) {
          that.timer--;
        },
        1000,
        this
      );
    },
    endTimer() {
      clearInterval(this.interval);
    }
  },
  created() {
    this.startTimer();
  }
};
</script>