<template>
  <article class="game__charface">

    <div
      class="game__char"
      v-if="!targetStatus || !targetLimit"
      :style='{backgroundImage: `url(/img/game/${facepic}.jpg)`}'
    >
      <img
        v-show="pop"
        class="game__char__icon2"
        :src="`/img/game/${facepic}.jpg`"
      >
    </div>

    <div
      v-else-if="targetStatus && targetLimit"
      @click="addToSkillQueue()"
      v-on-click-outside='cancel'
      class="game__char game__char--target"
    >
      <img :src="`/img/game/${facepic}.jpg`">
    </div>

    <Bars
      :energy='energy'
      :health='health'
    />
  </article>
</template>


<script>
import Bars from "@/components/_GaugeBars.vue";
import targetHandle from "@/helper/targetHandle";

export default {
  components: {
    Bars
  },
  mixins: [targetHandle],
  props: ["facepic"],
  computed: {
    targetStatus: function() {
      return this.$store.getters["targetMode/getTargetAllyStatus"];
    },

    targetLimit() {
      const s = this.$store.getters["targetMode/getSelectedSkillInfo"]
      if(s.skill.target === undefined) return false
      if (s.skill.target.type == "self") {
        if (s.caster == this.id) {
          return true;
        }
      } else if (s.skill.target.type == "otherAllies" || s.skill.target.type == "oneAlly") {
        if (s.caster != this.id) {
          return true;
        }
      } else if (s.skill.target.type == "allAllies") {
        return true;
      }

      return false;
    }
  },
  methods: {
    cancel: function() {
      this.$store.commit("targetMode/RESET_STATE", {isAlly:true})
    }
  }
};
</script>