<template>
  <div class="effectList">
    <img
      v-for="(effect, key) in activeEffectList(charIndex, team)"
      :key='key'
      :class="{'effectList__icon--enemy': isEnemy}"
      class="effectList__icon"
      :src="`/img/game/${effect.skillInfo.skillpic}.jpg`"
    >
    <img
      v-for="(effect, key) in effectList(charId)"
      :key='key'
      :class="{'effectList__icon--enemy': isEnemy}"
      class="effectList__icon"
      :src="`/img/game/${effect.skill.skillpic}.jpg`"
    >
  </div>
</template>

<script>
export default {
  props: ["charId", "isEnemy", "charIndex"],
  data(){
    return {
      team: ''
    }
  },
  computed: {
    effectList: function() {
      return this.$store.getters["ingame/getSkillQueueFilter"];
    },
    activeEffectList: function() {
      return this.$store.getters["ingame/getActiveEffects"];
    }
  },
  created(){
    if(this.isEnemy){
      this.team ='foes' 
    } else {
      this.team = 'friends'
    }
  }
};
</script>